<style>.check-text{opacity:.8;padding-left:0;display:table-cell}.checkmark{position:absolute;top:-1pt;left:-1pt;opacity:1;z-index:1000;background-size:1.25rem;display:inline-block}.check-text,.checkmark{float:left;vertical-align:middle;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;width:100%;height:70%}.check-text span,.checkmark span{text-align:center;vertical-align:top}.checkmark span{font-family:Gothic A1,sans-serif;color:#000;font-size:1.9rem;font-weight:400!important}.check-text span{font-family:Barlow Semi Condensed,sans-serif;font-weight:900;height:100%;width:90%;text-align:center;display:inline-block;box-sizing:content-box}.box{position:relative;pointer-events:visible}.box:after{content:"";display:table;clear:both}.number{margin-left:-.9rem;margin-top:-.5rem}</style>
<script context="module">
    export const defaultCss = {
        color: "black",
        backgroundColor: "transparent",
        size: "1.45em",
        borderWidth: "1.0pt"
    };
</script>
<script>
    //import {createEventDispatcher} from 'svelte'
    import Check from './small-check.svg';
    export let number = 1;
    export let css = defaultCss;
    export let checkedItem = {number:number,taken:false};

    function handleClick() {
        checkedItem.taken = !checkedItem.taken;
        //dispatch('checkedChanged', { checked: checkedItem.taken });
    }
</script>

<div    class="box"
        style="margin-left:0.5rem;margin-right:0.5rem;color:{css.color}; width:{css.size}; height:{css.size}; border-width:{css.borderWidth};
        border-color:{css.color}; border-style:solid; background-color:{css.backgroundColor};"
        on:click={handleClick}>
    <div class="check-text" style="width:{css.size}; height:{css.size}; font-size: {css.size}">
    <span class="number">
      {number}
    </span>
    </div>
    {#if checkedItem && checkedItem.taken}
        <div class="checkmark" style="width:100%; height:100%;">
            <div>
                <Check width="{css.size}" height="{css.size}"/>
            </div>
        </div>
    {/if}
</div>
