<style>:root{--logo-height:120px;--logo-width:280px;--logo-bar-bottom-padding:1rem;--info-width:calc(100% - var(--logo-width));--info-bar-height:calc(var(--logo-height) + var(--logo-bar-bottom-padding))}.logo{height:var(--logo-height);width:var(--logo-width);flex-grow:0;flex-shrink:0}.logo,h2{display:inline-block;margin:0}h2{margin-top:.9rem}.desc{margin-top:.5rem;resize:none;height:calc((var(--info-bar-height) - 3rem)*0.64)}.refresh{margin-left:.5rem;height:calc(var(--info-bar-height) - 3rem);width:calc(var(--info-bar-height) - 3rem);flex-shrink:0;flex-grow:0;border:2pt solid var(--input-border-color)}.refresh input{font-size:3rem;text-align:center;font-weight:700;font-family:Barlow Condensed,sans-serif}.refresh label{color:var(--input-border-color)}.refresh input{height:calc(var(--info-bar-height) - 4.4rem);width:calc(var(--info-bar-height) - 3.4rem)}.info-bar{display:flex;flex-direction:row;height:var(--info-bar-height);padding-bottom:var(--logo-bar-bottom-padding);width:100%}</style>

<script>
    import Logo from "./logo.svg"
    import Section from "../../shared/Section";

    export let characterData = {};
    export let title="ID";
    if (!title) {title="ID";}

</script>
<div class="info-bar">
    <Section {title} props={{style:"width:100%; margin-right:1rem"}}>
        <div class="flex-row">
            <div class="flex-column" style="flex-grow: 12">
                <input bind:value={characterData.name} width="100%" placeholder="Name" type="text" class="name"/>
                <textarea bind:value={characterData.description} type="text" placeholder="Description" class="desc"/>
            </div>
            <div class="refresh">
                <label for="refresh">Refresh</label>
                <input id="refresh" height="100%" type="number" bind:value={characterData.refresh} min="1" max="10" style="border:none"/>
            </div>
        </div>
    </Section>
    <div class="logo">
        <Logo height="100%" width="100%"/>
    </div>
</div>