<style>div{display:flex;flex-wrap:nowrap;flex-direction:row}mwc-switch{flex-grow:0;flex-shrink:0;margin:1.2rem}label{flex-grow:1;flex-shrink:1;width:100%;float:left;display:inline-block;margin-top:1rem}</style>

<script>
    import '@material/mwc-switch';
    export let value;
    export let title="";
    export let enabled=true;
    $:checked = value ? "checked" : "";
    $:disabled = enabled ? "" : "disabled";
</script>
<div style="width:100%">
{#if !!title}
    <label for="switch">{title}</label>
{/if}
<mwc-switch id="switch"  {checked} {disabled} on:change={()=>value=!value}/>
</div>